<template>
  <div class="lottery_cateogory ">
		<div class="firework_area"></div>

		<div class="click_lottery_dispatch"
      v-on:click="this.renderTable"
    ></div>

    <div class="container-fluid">
      <input type="hidden" v-model="no_of_rows" />

      <div class=" mt-2 ">
        <img :src="this.logo" alt="lottery" id="logo_image">

        <h1 class="text-primary font-family-bold mt-3">
          <b>LOTTERY ANNOUNCEMENT</b>
        </h1>
      </div>

      <div class=" mt-4 row text-center">
        <!-- <div class="col-md-6">
          <button class="btn btn-outline-primary pull-left" v-on:click="this.renderTable">
            Declare Lottery
          </button>
        </div> -->
        <div class="col-md-12">
          <div class="text-dark" >
            <strong>
              <h3>
                <b>
                <span class="text-dark">Category -</span> 
                <span class="text-dark">&ensp;{{ this.category_title }}</span>
                </b>
              </h3>
              <div class="text-secondary mt-5 click_buzzer_text" style="margin-top: 40vh !important;" >Click the buzzer to declare lottery results</div>
            </strong>

          </div>
        </div>
      </div>


      <div class="div_table">
        <table class="table table-striped mt-3" v-if="this.showTable == true">
          <thead>
            <tr>
              <th scope="col" v-for="(heading, key) in this.headings" v-bind:key="key">{{ heading }}</th>
            </tr>
          </thead>
          <tbody>
            <tr
              v-for="(row, key) in this.show_json_datas"
              class="fade-enter-active"
              v-bind:key="key"
            >
              <td
                v-for="(item, item_k) in row"
                v-bind:key="item_k"

              >
                <span
                  v-if="item_k != 'is_mandatory' && item_k != 'show'"
                >
                  {{ item }}
                </span>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
      
      <div class="container mt-5" v-if="this.show_json_datas.length == this.show_no_of_rows">
        <!-- <button class="btn btn-outline-primary pull-right btn-sm" v-on:click="this.csvExport(this.csvData)">
          <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-arrow-down-square-fill" viewBox="0 0 16 16">
            <path d="M2 0a2 2 0 0 0-2 2v12a2 2 0 0 0 2 2h12a2 2 0 0 0 2-2V2a2 2 0 0 0-2-2H2zm6.5 4.5v5.793l2.146-2.147a.5.5 0 0 1 .708.708l-3 3a.5.5 0 0 1-.708 0l-3-3a.5.5 0 1 1 .708-.708L7.5 10.293V4.5a.5.5 0 0 1 1 0z"/>
          </svg> 
          CSV
        </button> -->
        <button class="btn btn-outline-primary pull-right btn-sm" v-on:click="this.excelExport()">
          <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-arrow-down-square-fill" viewBox="0 0 16 16">
            <path d="M2 0a2 2 0 0 0-2 2v12a2 2 0 0 0 2 2h12a2 2 0 0 0 2-2V2a2 2 0 0 0-2-2H2zm6.5 4.5v5.793l2.146-2.147a.5.5 0 0 1 .708.708l-3 3a.5.5 0 0 1-.708 0l-3-3a.5.5 0 1 1 .708-.708L7.5 10.293V4.5a.5.5 0 0 1 1 0z"/>
          </svg> 
          DOWNLOAD EXCEL
        </button>
      </div>
      <div id="hidden_box" style="display:none"></div>
    </div>

  </div>

</template>


<script>
// import table2excel from "//cdn.rawgit.com/rainabba/jquery-table2excel/1.1.0/dist/jquery.table2excel.min.js";

import axios from "axios";
import $ from "jquery";

import { Fireworks } from "fireworks-js";

import logo from '@/assets/logo.png';

// import downloadcsv from 'vue-json-csv'
// import JsonCSV from 'vue-json-csv'

// Vue.component('downloadCsv', JsonCSV);

export default {
  name: "LotteryCategory",
  components: {
    // downloadcsv,
  },
  data: function () {
    return {
      message: "Hello Vue.js!",
      category_id: this.$route.params.id,
      category_title: null,
      headings: [],
      no_of_rows: 0,
      show_no_of_rows: 5,
      all_json_datas: [],
      show_json_datas: [],
      show_json_datas_without_cols: [],
      showRowDelayMilliSeconds: 500,
      int: null,
      showTable: false,
      fireworks: null,
      no_of_cols: null,
      // logo_url: `${window.location.origin}/logo.png`,
      logo_url: `/logo.png`,
    };
  },
  created() {
    this.logo = logo;
    console.log("category id is");
    console.log(this.category_id);

    axios
      .get(`${window.location.origin}/categories/${this.category_id}.json`)
      .then((response) => {
        this.all_json_datas = response.data.rows;
        this.category_title = response.data.details.category;
        this.no_of_cols = response.data.details.no_of_columns;

        var a_row = this.all_json_datas[0];
        var heading = [];

        Object.entries(a_row).map((element, ind) => {
          if((ind + 1) !== this.no_of_cols) {
            heading.push(this.stringToHeading(element[1]));
          }
        });

        this.headings = heading;
      })
      .catch((e) => {
        console.error(e);
      });
  },
  mounted() {
    const options = {
      speed: 80,
			acceleration: 10,
			gravity: 1,
			particles: 200,
			explosion: 10,
      sound: { enable: false },
			delay: { min: 5, max: 90 },
    }

    // const container = document.querySelector('#router-view')
    const container = document.querySelector(".firework_area");

    const fireworks = new Fireworks(container, {
      options
    });

    // fireworks.pause()
    fireworks.setSize({ height: 500, width: 500 });

    this.fireworks = fireworks;

		this.fireworks.clear();
		this.fireworks.start();


    // $('.click_lottery_dispatch').on('click', function() {
      // console.log('click_lottery_dispatch');
      // this.renderTable;
    // });
  },
  methods: {
    renderTable: function () {
      $('.click_buzzer_text').hide();

      // console.log($('.container'));
      // this.show_json_datas = [];
      // this.showTable = false;
      this.showTable = true;
      // console.log(this.showTable);
      this.no_of_rows = this.show_no_of_rows;

      // var no_of_rows = this.no_of_rows;

      var used_json_data_ids = [];

      // get all mandatory lotterys
      var mandatory_json_datas = this.all_json_datas.filter((data, ind) => {
        if (data.is_mandatory == true) {
          used_json_data_ids.push(ind);
          return true;
        } else {
          return false;
        }
      });

      // set show as false by default
      mandatory_json_datas = mandatory_json_datas.map((dt) => {
        // dt = dt;
        dt.show = false;
        return dt;
      });

      var show_json_datas = mandatory_json_datas;

      var required_non_mandatory_data_count =
      this.no_of_rows - mandatory_json_datas.length;

      // get non mandatory lottery
      if (required_non_mandatory_data_count > 0) {
        while (show_json_datas.length < this.show_no_of_rows) {
          var random_index = Math.floor(
            Math.random() * this.all_json_datas.length
          );

          if (!used_json_data_ids.includes(random_index)) {
            used_json_data_ids.push(random_index);
            var dt = this.all_json_datas[random_index];
            // set show as false by default
            dt.show = false;
            show_json_datas.push(dt);
          }
        }
      }

      this.show_json_datas = [];

      show_json_datas = this.shuffle(show_json_datas);

      // this.show_json_datas.push(this.headings);

      // render with delay
      var i = 0;
      this.int = setInterval(() => {
        this.show_json_datas.push(show_json_datas[i]);
        this.no_of_cols = this.headings.length;

        i++;
        if (i == this.show_no_of_rows) {
          clearInterval(this.int);
        }

      }, this.showRowDelayMilliSeconds);

    },

    shuffle(a) {
      for (let i = a.length - 1; i > 0; i--) {
        const j = Math.floor(Math.random() * (i + 1));
        [a[i], a[j]] = [a[j], a[i]];
      }
      return a;
    },

    csvExport(arrData) {
      let csvContent = "data:text/csv;charset=utf-8,";
      
      csvContent += [
        // header.join(","),
        this.headings.join(","),
        ...arrData.map(item => {
          var arItem = Object.values(item);
          var pop_count = arItem.length - this.headings.length;

          for(let i = 0; i < pop_count; i++) {
            arItem.pop();
          }
          arItem.join(",")
          return arItem;
          // Object.values(item).join(",")
        })
      ]
        .join("\n")
        .replace(/(^\[)|(\]$)/gm, "");

      const data = encodeURI(csvContent);
      const link = document.createElement("a");
      link.setAttribute("href", data);
      link.setAttribute("download", `${this.category_title}_csv.csv`);
      link.click();
    },

    excelExport() {
      
      var data = this.show_json_datas;
      
      var str_data = '';
      
      this.headings.forEach(element => {
        str_data = str_data + this.stringToHeading(element) + '\t';
      })
      str_data = str_data + '\n';
      
      // data rows for excel file
      data.map(dt => {
        var val = '';
        Object.entries(dt).map((d, t) => {
          val = d[1];
          if(t < this.headings.length ) {
            str_data = str_data + val +'\t';
          }
        });
        str_data = str_data + '\n'
        return dt;
      });

      //make a blob
      data = str_data;
      var excel_blob = new Blob([data], {type:'data:application/vnd.ms-excel;base64'}); 
      
      //create a link with the href of the blob and click on it
      var link = $("<a>").attr({
        href : window.webkitURL.createObjectURL(excel_blob),
        download : `${this.category_title}_xls.xls`
      }).appendTo("#hidden_box")
      link.get(0).click()
      
      
    },

    stringToHeading(str) {
      var temp = str.replace('_', ' ');
      temp = temp.charAt(0).toUpperCase() + temp.slice(1);
      return temp;
    }
  },
  computed: {
    csvData() {
      return this.show_json_datas.map(item => ({
        ...item,
      }));
    }
  }


};
</script>

